import request from 'reqwest';
import cons from '../constants/BaseConstants';
import AppActions from '../actions/AppActions';

class AppService {
  getInvoice(callback) {
    return request({
      url: cons.INVOICE_URL,
      method: 'GET',
      crossOrigin: true,
      type: 'json'
    })
      .then(function(response) {
        callback();
        AppActions.getInvoice(response);
      }, function (error) {
        console.log(error);
      });
  }
}

export default new AppService()



