import React from 'react';
import moment from 'moment';
import 'moment/locale/ru'
import _ from 'lodash';

export default class Invoice extends React.Component {

  money(number) {
    if (Number(number) === number && number % 1 !== 0) {
      return _.round(number, 3).toLocaleString(undefined, { minimumFractionDigits: 3 });
    } else {
      return number;
    }
  }

  render() {
    var price = 0, count = 0, countProducts, invoiceRow = [];
    var invoice = this.props.data;
    var money = this.money;
    if (invoice) {
      invoice.OrderDetails.forEach(function (item, key) {
        price = price + (item.Price * item.Quantity);
        invoiceRow.push(
          <div className="row" key={key}>
            <div className="cell text-center" style={{width:'100px'}}>{key + 1}</div>
            <div className="cell text-center" style={{width:'300px'}}>{item.Product}</div>
            <div className="cell text-center" style={{width:'130px'}}>{money(item.Quantity)} {item.PerUnit}</div>
            <div className="cell text-center" style={{width:'160px'}}>{money(item.Price)} сом</div>
            <div className="cell text-center" style={{width:'160px'}}>{money(item.Price * item.Quantity)} сом</div>
          </div>
        )
      });
      count = _.sumBy(invoice.OrderDetails, 'Quantity');
      countProducts = invoice.OrderDetails.length;
      return (
        <div className="wrap" style={{pageBreakAfter: 'avoid'}}>
          <div className="title-invoice">
            Расходная накладная №{ invoice.Order.OrderId } от { moment(invoice.Order.OrderDate).format('dddd') }
            , { moment(invoice.Order.OrderDate).format('DD MMM YYYY hh:mm') }
          </div>
          <div className="title-sub">
            <div className="label">Поставщик </div>
            <div className="text"></div>
          </div>
          <div className="title-sub customer">
            <div className="label">Покупатель </div>
            <div className="text">{ invoice.Order.Customer }</div>
          </div>
          <div className="table">
            <div className="row">
              <div className="cell text-center" style={{width:'100px'}}>Номер</div>
              <div className="cell text-center" style={{width:'300px'}}>Товар</div>
              <div className="cell text-center" style={{width:'130px'}}>Количество</div>
              <div className="cell text-center" style={{width:'160px'}}>Цена</div>
              <div className="cell text-center" style={{width:'160px'}}>Сумма</div>
            </div>
            {invoiceRow}
          </div>
          <div className="table no-border total">
            <div className="row">
              <div className="cell text-right" style={{width:'450px'}}>Общее количество</div>
              <div className="cell text-right" style={{width:'450px'}}>{money(count)}</div>
            </div>
            <div className="row">
              <div className="cell text-right" style={{width:'450px'}}>Итого</div>
              <div className="cell text-right" style={{width:'450px'}}>{money(price)}</div>
            </div>
            <div className="row">
              <div className="cell text-right" style={{width:'450px'}}>в том числе НДС</div>
              <div className="cell text-right" style={{width:'450px'}}>___________сом</div>
            </div>
            <div className="row">
              <div className="cell text-right" style={{width:'450px'}}>в том числе НФР</div>
              <div className="cell text-right" style={{width:'450px'}}>___________сом</div>
            </div>
          </div>
          <div className="total-box">
            Всего наименовании { countProducts } на сумму {money(price)} сом
          </div>
          <div className="table no-border ">
            <div className="row">
              <div className="cell text-left" style={{width:'450px'}}>Руководитель</div>
              <div className="cell text-left" style={{width:'450px'}}>Бухгалтер/менеджер</div>
            </div>
            <div className="row">
              <div className="cell text-left" style={{width:'450px'}}>Отпустил ________________________________</div>
              <div className="cell text-left" style={{width:'450px'}}>Получил ________________________________</div>
            </div>
          </div>
        </div>
      );
    } else {
      return (<div></div>);
    }

  }
}


