import React, { Component } from 'react';
import cons from '../constants/BaseConstants';
import store from 'store';

class Login extends Component {
  constructor() {
    super();
    this.state = {
      user: '',
      password: '',
      busy: false
    };
  }

  handleInputChange(event) {
    const target = event.target;
    const value = target.type === 'checkbox' ? target.checked : target.value;
    const name = target.name;
    this.setState({
      [name]: value
    });
  }

  login() {
    if (this.state.password === cons.PASSWORD && this.state.user === cons.USER) {
      store.set('inv_login',{login: true, expire: new Date().getTime() + 3600000});
      // eslint-disable-next-line no-restricted-globals
      location.reload();
    } else {
      alert('Не правильный пароль или логин')
    }
  }

  render() {
    return (
      <div>
        <div className="form">
          <h2>Вход</h2>
          <div className="row">
            <label htmlFor="user">Логин</label> <input id="user" name="user" type="text" onChange={this.handleInputChange.bind(this)}/>
          </div>
          <div className="row">
            <label htmlFor="pass">Пароль</label> <input id="pass" name="password" type="password" onChange={this.handleInputChange.bind(this)}/>
          </div>
          <button onClick={this.login.bind(this)}>Вход</button>
        </div>
      </div>
    );
  }
}

export default Login;



