import cons from '../constants/BaseConstants';
import BaseStore from './BaseStore';

class InvoiceStore extends BaseStore {

  constructor() {
    super();
    this.subscribe(() => this._registerToActions.bind(this));
    this._invoice = '';
  }

  _registerToActions(action) {
    switch(action.actionType) {
      case cons.INVOICE_GET:
        this._invoice = action.invoice;
        this.emitChange();
        break;
      default:
        break;
    };
  }

  get invoice() {
    return this._invoice;
  }
}

export default new InvoiceStore();



